import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { FormControl, FormBuilder, FormGroup, Validators} from '@angular/forms';
import { ToasterService} from 'angular5-toaster';
import { Tree } from '@angular/router/src/utils/tree';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss'],
  animations: [routerTransition()]

})
export class ChangePasswordComponent implements OnInit {
  societyadmin = JSON.parse(localStorage['societyadmin']);

  manager;
  managerexists: boolean = false;
  chairman;
  chairmanexists: boolean = false;


  validpassword: boolean= false;

  current_password: string;
  new_password: string;
  confirm_password : string;

  firstFormGroup: FormGroup;

  private toasterService: ToasterService;

  constructor( private http: HttpClient,
    toasterService: ToasterService,
    private _formBuilder: FormBuilder) { 

      this.toasterService = toasterService;

      if (localStorage.getItem('manager')) {

        this.manager = JSON.parse(localStorage['manager']);
        this.managerexists = true;

    }
    console.log('ManagerExists:' + this.managerexists);

      if (localStorage.getItem('chairman')) {

        this.chairman = JSON.parse(localStorage['chairman']);
        this.chairmanexists = true;
  
    }
    console.log('ChairmanExists:' + this.chairmanexists);

    this.firstFormGroup = this._formBuilder.group({
      current_passwordCtrl: ['', Validators.required],
      new_passwordCtrl: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(12)]],
      confirmpasswordCtrl: ['', [Validators.required, Validators.pattern]]
    });

  }
  get current_passwordCtrl() { return this.firstFormGroup.get('current_passwordCtrl'); }
  get new_passwordCtrl() { return this.firstFormGroup.get('new_passwordCtrl'); }
  get confirmpasswordCtrl() { return this.firstFormGroup.get('confirmpasswordCtrl'); }



  ngOnInit() {
  }


  checkcurrentpassword(){
    console.log("Checking current password");
    if(this.chairmanexists == true){
      const chairman_password= {
        password: this.current_password
      }
      console.log(chairman_password);
      this.http.post('api/checkchairmancurrentpassword/' + this.chairman.id , chairman_password).subscribe(data=> {
        console.log(data)  
        var response
        response= data;
        if(response.success == true){
          this.toasterService.pop('success', response.message); 
          this.validpassword = true;
        }
        else{
          this.toasterService.pop('error', response.message);  
          this.validpassword = false;

        }
   });
    }
    else if(this.managerexists == true){
      const manager_password= {
        password: this.current_password
      }
      console.log(manager_password);
      this.http.post('api/checkmanagercurrentpassword/' + this.manager.id , manager_password).subscribe(data=> {
        console.log(data)  
        var response
        response= data;
        if(response.success == true){
          this.toasterService.pop('success', response.message); 

        }
        else{
          this.toasterService.pop('error', response.message);  

        }
   });
    }
    else{      
      const society_password= {
      password: this.current_password
    }
    console.log(society_password);
    this.http.post('api/checksocietycurrentpassword/' + this.societyadmin.id , society_password).subscribe(data=> {
      console.log(data)  
      var response
      response= data;
      if(response.success == true){
        this.toasterService.pop('success', response.message); 
        this.validpassword = true;

      }
      else{
        this.toasterService.pop('error', response.message);  
        this.validpassword = false;

      }
 });

    }


  }

  changepassword(){
    if(this.chairmanexists == true){
      const chairman_password= {
        password: this.current_password,
        newpassword: this.confirm_password
      }
      console.log(chairman_password);
      this.http.put('api/changechairmancurrentpassword/' + this.chairman.id , chairman_password).subscribe(data=> {
        console.log(data)  
        var response
        response= data;
        if(response.success == true){
          this.toasterService.pop('success', response.message); 

        }
        else{
          this.toasterService.pop('error', response.message);  

        }
   });
    }
    else if(this.managerexists == true){
      const manager_password= {
        password: this.current_password,
        newpassword: this.confirm_password
      }
      console.log(manager_password);
      this.http.put('api/changemanagercurrentpassword/' + this.manager.id , manager_password).subscribe(data=> {
        console.log(data)  
        var response
        response= data;
        if(response.success == true){
          this.toasterService.pop('success', response.message); 

        }
        else{
          this.toasterService.pop('error', response.message);  

        }
   });
    }
    else{      
      const society_password= {
        password: this.current_password,
        newpassword: this.confirm_password
    }
    console.log(society_password);
    this.http.put('api/changesocietycurrentpassword/' + this.societyadmin.id , society_password).subscribe(data=> {
      console.log(data)  
      var response ;          
      response= data;
      if(response.success == true){
        this.toasterService.pop('success', response.message); 

      }
      else{
        this.toasterService.pop('error', response.message);  

      }
 });

    }

  }
}



